export default {
  data() {
    return {
      navList: ['商品', '详情', '评价', '推荐'],   // 顶部导航栏
      navIndex: 0,                          // 顶部初始索引值
      top: 0,                               // 滚动距离顶部距离
      indicatorDots: true,                  // 指示点显隐
      autoplay: true,                       // 自动轮播
      interval: 2000,                       // 自动轮播时间 
      duration: 500,                        // 轮播过渡时间
      indicatorActiveColor: '#fff',         // 轮播图指示点选中颜色
      swiperList:[{}, {}, {}],              // 轮播元素数组
      isShowTop: true,                      // 顶部
      data: {
        price: 1099,
        textList: ['包邮', '自营'],
        title: '',                             
      },                                     // 商品价格等
      store: {
        name: 'SONY官方自营店',
        imgUrl: ''   
      },                                      // 店名头像信息
      showPanic: false,                       // 顶部分享显示与隐藏
      coverShow: false,                       // 全局遮罩层显隐
      sale_info: [{
        title: '满送',
        info: '满999元送4000毫安的充电宝,购买后送200积分',
        type: 1
      }, {
        title: '促销',
        info: '满1548元，省150元',
        time: '2019.06.12-2019.06.15',
        type: 2
      }],
      normShow: false,                        // 商品规格弹窗
    }
  },
  // 接受首页传递的参数
  onLoad(option) {
    console.log('分享文章详情页接受到的参数',option)
    this.data.title = option.info
    if(option.panic === 'true') {
      this.showPanic = true
      return
    }
  },
  methods: {
    // 返回
    goBack() {
      uni.navigateBack({
        delta: 1
        })
    },
    navAction(index) {
      this.navIndex = index
    },
    // 分享
    goShare() {
      uni.share({
        provider: "weixin",
        scene: "WXSceneSession",
        type: 0,
        href: "http://uniapp.dcloud.io/",
        title: "uni-app分享",
        summary: "我正在使用HBuilderX开发uni-app，赶紧跟我一起来体验！",
        imageUrl: "https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/uni@2x.png",
        success: function (res) {
          console.log("success:" + JSON.stringify(res));
        },
        fail: function (err) {
          console.log("fail:" + JSON.stringify(err));
        }
      })
    },
    // 收藏
    keepAction() {
      console.log('点击了收藏')
    },
    // 页面滚动
    scroll(e) {
      this.top = e.detail.scrollTop
      if (e.detail.scrollTop > 375) {
        this.isShowTop = false
        return
      }
      if (e.detail.scrollTop <= 375) {
        this.isShowTop = true
        return
      }
    },
    // 点击促销信息
    lookInfo() {
      this.coverShow = true
    },
    goShopcar() {
      uni.switchTab({
        url: '../shopcar/shopcar'
      })
    }
  },
}
